.catalog-additional__container {
  padding-top: 25px;
  padding-bottom: 35px;
  @media (min-width: $tablet-width) {
    padding-top: 68px;
    padding-bottom: 69px;
  }
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(4, 1fr); //  ,     .catalog-addition__promo
    gap: 80px; // __promo           catalog-aditional__list
    padding-top: 75px;
    padding-bottom: 86px;
  }
}
.catalog-additional__heading {
  grid-column: 1 / -1;
  margin: 0;
  margin-bottom: 36px;
  color: $black;
  font-family: $font-heading;
  font-size: 24px;
  font-weight: 400;
  line-height: 1; /* 100% */
  @media (min-width: $tablet-width) {
    font-size:  36px;
  }
  @media (min-width: $desktop-width) {
    grid-column: 1 / -1;
    margin-bottom: 0;
  }

  span {
    position: relative; //   z-index     before (  )
    z-index: 1;
    background-color: $white;
    width: max-content;
    padding-left: 22px;
    padding-right: 22px;
    margin-left: -22px;
    display: flex; //            (         )
    @media (min-width: $tablet-width) {
      padding-left: 40px;
      padding-right: 40px;
      margin-right: -40px;
    }
  }
}

.catalog-additional__heading::before { //    before,             after!!!      position: relative;       margin-top;           field-group__heading
  position: absolute; //          static       html
  content: "";
  height: 2px;
  left: 0;
  right: 0;
  margin-top: 16px;
  background-color: $color-primary;
  @media (min-width: $tablet-width) {
    margin-top: 22px;
  }
}

.catalog-additional__list {
  grid-column: 1 / 4;
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-bottom: 35px;
  @media (min-width: $tablet-width) {
    margin-bottom: 69px;
  }
  @media (min-width: $desktop-width) {
    margin-bottom: 0;
  }
}
