.promo {
  padding-top: 8px; //          
  text-align: center;
  position: relative;

  &::before, //     before      after 
  &::after { 
    z-index: -2; //      ,    -1,         .promo__image   z-index        (promo::after)
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 302px;
    @media (min-width: $tablet-width) { //     
      display: none;
    }
    @media (min-width: $desktop-width) {
      display: block;
      top: -200px; //       200px
      right: 0; //    
      bottom: 0; //    
      left: 50%; //      
      height: 694px;
    }
  }

  &::before {
    background-color: $color-primary;
    background-size: 320px 302px;
    background-repeat: no-repeat;
    background-image: url("../../img/background/bg-promo-mobile.jpg");
    background-position: 50% 50%;

    @media (min-resolution: 2dppx) {
      background-image: url("../../img/background/bg-promo-mobile@2x.jpg");
    }

    @media (min-width: $desktop-width) {
      background-position: left 50%;
      background-size: 720px 694px;
      background-image: url("../../img/background/bg-promo-desktop.jpg");
      
      @media (min-resolution: 2dppx) {
        background-image: url("../../img/background/bg-promo-desktop@2x.jpg");
      }
    }

  }

  &::after {
    background-color: rgba(104, 183, 56, 0.85); //    after   before     
  }

  @media (max-width: $mobile-width-only) {
    color: $white; //       
  }

  @media (min-width: $tablet-width) {
    color: $black; //  :   ,
    background-image: none; //  ,
    background-color: transparent; //  
    text-align: left; //     
  }
}

.promo__container { // e    c container,                
  @media (min-width: $tablet-width) {
    position: relative; //   -
    padding: 0 90px 526px; // 90px        , 526px   ( )    
  }
  @media (min-width: $desktop-width) {
    min-height: 582px; //    
    padding: 20px 610px 95px 80px; // 80px
  }
}

.promo__heading {  // :    ....
  margin: 0;
  margin-bottom: 25px;
  font-size: 36px;
  line-height: 1;
  font-family: $font-heading;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    margin-bottom: 41px;
    font-size: 60px;
  }

}

.promo__text {
  margin-top: 0;
  margin-bottom: 29px;

  font-family: $font-heading;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    margin-bottom: 49px;
  }
}

.promo__image {
  width: 280px;
  @media (min-width: $tablet-width) {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    width: 709px;
    height: 609px;
  }
  @media (min-width: $desktop-width) {
    top: 0;
    left: calc(50% - 162px); //           162px;
    width: 552px;
    height: 499px;
  }
}

.promo__button {
  width: 100%;
}
