.logo { //     c @import "blocks/logo.scss";  style.scss
  display: grid; //   ,    .
  grid-template-columns: 34px 1fr; //           .page-header__bar,     logo.
  align-items: center; //           .page-header__logo
  gap: 16px; //              .page-header__bar

  @media (min-width: $tablet-width) {
    display: block;
  }
  
  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.logo__sign-wrapper {
  width: 101px;  //    picture        ,              
  height: 18px;
  justify-self: center; //       
}

.logo__sign {
  @media (min-width: $tablet-width) {
    display: none;
  }
}

.page-footer__logo {
  grid-template-columns: auto;
  margin-left: auto;
  margin-right: auto;
  width: 128px;
  height: 24px;
}
